<template>
  <v-card>
    <v-card-title class="pt-4 pb-0">
      <h4>{{ $t('form.register') }}</h4>
    </v-card-title>

    <v-card-text class="pa-4">
      <v-form v-model="formValid">
        <v-text-field
          v-model="form.email"
          :label="$t('form.email')"
          :rules="[rules.required, rules.email]"
          required
        />
        <v-text-field
          v-model="form.password"
          :label="$t('form.password')"
          type="password"
          :rules="[rules.required]"
          required
        />
        <v-text-field
          v-model="form.fullName"
          :label="$t('form.fullName')"
          :rules="[rules.required]"
          required
        />
      </v-form>

      <v-btn
        color="primary"
        variant="elevated"
        :disabled="!formValid || loading"
        :loading="loading"
        @click="handleRegister"
      >{{ $t('common.send') }}</v-btn>

      <p class="auth-form__help">
        {{ $t('form.alreadyHaveAccount') }}
        <a href="#" @click.prevent="$emit('switchForm')">
          {{ $t('form.signIn') }}
        </a>
      </p>
    </v-card-text>

    <div class="form-overlay" v-if="isOverlayVisible">
      <div class="form-overlay__content">
        <template v-if="loading">
          <v-progress-circular indeterminate color="primary" />
        </template>
        <template v-else>
          <v-icon color="primary" size="x-large">
            mdi-check
          </v-icon>
          <p class="mb-4 mt-4">Registration complete. Welcome aboard!</p>
          <a
            v-if="context === 'order'"
            href="#"
            @click.prevent="hiddenLogin"
          >
            Back to checkout
          </a>
          <a v-else
             href="#"
             @click.prevent="$emit('switchForm')"
          >
            {{ $t('form.signIn') }}
          </a>
        </template>
      </div>
    </div>
  </v-card>
</template>

<script>
import { ref, reactive, computed } from 'vue';
import { useUserStore } from "@/stores/userStore";

export default {
  props: {
    context: String,
  },
  setup(_, { emit }) {
    const userStore = useUserStore();

    const form = reactive({
      fullName: '',
      email: '',
      password: '',
    });

    const formValid = ref(false);

    const showSuccessMessage = ref(false);

    const rules = {
      required: (v) => !!v || "This field is required",
      email: (v) => /.+@.+\..+/.test(v) || "Email must be valid",
    };

    const handleRegister = () => {
      if (formValid.value) {
        try {
          userStore.register(form);
          showSuccessMessage.value = true;
        } catch (error) {
          console.error(error);
        }
      }
    };

    const hiddenLogin = async () => {
      await userStore.login(form);
      emit('authComplete');
    };

    const loading = computed(() => userStore.isLoading);

    const isOverlayVisible = computed(() => showSuccessMessage.value || loading.value);

    return {
      loading,
      form,
      formValid,
      rules,
      isOverlayVisible,
      hiddenLogin,
      handleRegister,
    };
  },
};
</script>
